.breakpoint-info {
  z-index: 9999;
  position: fixed;
  right: 0;
  bottom: 32px; }

.breakpoint-info > div {
  padding: 10px 15px;
  display: none;
  color: #333;
  font-family: sans-serif;
  font-size: 20px;
  font-weight: bold; }

@media (max-width: 575px) {
  .breakpoint-info .bp-xs {
    display: block;
    background-color: #E74C3C; } }

@media (min-width: 576px) and (max-width: 767px) {
  .breakpoint-info .bp-sm {
    display: block;
    background-color: #2ECC71; } }

@media (min-width: 768px) and (max-width: 991px) {
  .breakpoint-info .bp-md {
    display: block;
    background-color: #3498DB; } }

@media (min-width: 992px) and (max-width: 1199px) {
  .breakpoint-info .bp-lg {
    display: block;
    background-color: #F1C40F; } }

@media (min-width: 1200px) {
  .breakpoint-info .bp-xl {
    display: block;
    background-color: #E67E22; } }

