/* size */
.inflate {
  display: block;
  height: 100%; }

/* name-me */
/* embeds */
.embed-container {
  position: relative;
  display: block;
  height: 0;
  max-width: 100%;
  overflow: hidden;
  padding-bottom: 56.25%; }
  .embed-container.issuu {
    padding-bottom: 41%; }
    .embed-container.issuu > div > div > div:last-child {
      display: none; }
  .embed-container > iframe,
  .embed-container > embed,
  .embed-container > object,
  .embed-container > video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

/* aspect ratio height */
/* clearfix */
.clearfix::after {
  display: block;
  content: '';
  clear: both; }

/* other */
.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

/* columns */
.columns {
  display: flex;
  justify-content: space-between; }
  .columns > * {
    flex: auto; }

@media (max-width: 575px) {
  .container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px; } }

@media (min-width: 576px) {
  .container {
    margin-left: auto;
    margin-right: auto;
