/* components */
body {
  background-color: #202020;
  line-height: 1.4;
  font-family: Arial;
  font-size: 18px;
  color: #fff; }
  @media (max-width: 575px) {
    body {
      padding-top: 15px; } }
  @media (min-width: 576px) {
    body {
      padding-top: 50px;
      padding-bottom: 30px; } }

input[type=text] {
  width: 100%;
  padding: 10px 15px;
  border: 0;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.05);
  font-size: 22px;
  color: #fff; }
  input[type=text]::placeholder {
    opacity: 0.2; }

ul {
  margin-top: 30px;
  list-style-type: none; }
  @media (min-width: 576px) {
    ul {
      padding: 0 15px; } }

li {
  padding-top: 20px;
  padding-bottom: 20px;
  display: grid; }
  @media (max-width: 575px) {
    li {
      grid-template-columns: auto auto; } }
  @media (min-width: 576px) {
    li {
      grid-template-columns: 36px 36px 1fr;
      grid-gap: 30px; } }
  li .link {
    font-size: 0; }
    @media (min-width: 576px) {
      li .link {
        display: flex;
        justify-content: center;
        align-items: center; } }
  li svg {
    display: inline-block;
    height: 36px; }
  li .nolink {
    opacity: 0.2; }
  li .name {
    font-family: 'Viga', sans-serif;
    font-weight: 400;
    font-style: normal;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 18px;
    cursor: default; }
    @media (max-width: 575px) {
      li .name {
        grid-column-end: span 2;
        padding-top: 20px; } }
    @media (min-width: 576px) {
      li .name {
        display: flex;
        align-items: center; } }
  li a:hover svg, li a:focus svg {
    transition: color 0.2s ease-in-out 0s;
    color: #ffc016; }
